import * as React from "react";
/**
 * @description Hoc
 * @param initSetting 
 * @param OldComponent 
 * @returns 
 *
 */

const Hoc = initSetting => OldComponent => {
  return class extends React.PureComponent {
    render() {
      let {
        className,
        defaultStyles
      } = this.props; // className

      const initedProps = { ...initSetting,
        ...this.props,
        className: (initSetting.className || "") + " " + (className || ""),
        defaultStyles: initSetting.defaultStyles + defaultStyles
      };
      return React.createElement(OldComponent, initedProps);
    }

  };
};

export default Hoc;